.card {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80vw;
  padding: 10px auto;
}

.card-img {
  width: 30vw;
  height: auto;
}

.card-body {
  width: 70vw;
  padding: 10px;
}

.hidden {
  display: none;
}

.loader {
  display: block;
  position: relative;
  width: 100px;
  height: 16px;
  margin: 10px 0;
}

.loader:before,
.loader:after {
  content: '';
  position: absolute;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #fff;
  box-shadow: 32px 0 #fff;
  left: 0;
  top: 0;
  animation: ballMoveX 2s linear infinite;
}

.loader:after {
  box-shadow: none;
  transform: translateX(64px) scale(1);
  z-index: 2;
  animation: none;
  animation: trfLoader 2s linear infinite;
}

@keyframes trfLoader {
  0%,
  5% {
    transform: translateX(64px) scale(1);
    background: #fff;
  }
  10% {
    transform: translateX(64px) scale(1);
    background: #ff3d00;
  }
  40% {
    transform: translateX(32px) scale(1.5);
    background: #ff3d00;
  }
  90%,
  95% {
    transform: translateX(0px) scale(1);
    background: #ff3d00;
  }
  100% {
    transform: translateX(0px) scale(1);
    background: #fff;
  }
}
@keyframes ballMoveX {
  0%,
  10% {
    transform: translateX(0);
  }
  90%,
  100% {
    transform: translateX(32px);
  }
}
