.card {
  width: 80vw;
  justify-content: center;
  align-items: center;
  padding: 10px auto;
  display: flex;
}

.card-img {
  width: 30vw;
  height: auto;
}

.card-body {
  width: 70vw;
  padding: 10px;
}

.hidden {
  display: none;
}

.loader {
  width: 100px;
  height: 16px;
  margin: 10px 0;
  display: block;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  width: 16px;
  height: 16px;
  background: #fff;
  border-radius: 50%;
  animation: ballMoveX 2s linear infinite;
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: 32px 0 #fff;
}

.loader:after {
  box-shadow: none;
  z-index: 2;
  animation: trfLoader 2s linear infinite;
  transform: translateX(64px)scale(1);
}

@keyframes trfLoader {
  0%, 5% {
    background: #fff;
    transform: translateX(64px)scale(1);
  }

  10% {
    background: #ff3d00;
    transform: translateX(64px)scale(1);
  }

  40% {
    background: #ff3d00;
    transform: translateX(32px)scale(1.5);
  }

  90%, 95% {
    background: #ff3d00;
    transform: translateX(0)scale(1);
  }

  100% {
    background: #fff;
    transform: translateX(0)scale(1);
  }
}

@keyframes ballMoveX {
  0%, 10% {
    transform: translateX(0);
  }

  90%, 100% {
    transform: translateX(32px);
  }
}

